const Header = ({ setMode, setSentense, setDescription }) => {
	const handleChangeLang = () => {
		setSentense("");
		setMode("lang");
		setDescription(
			"類似度の高い単語を表示します。\n単語を足し引きすることもできます。"
		);
	};
	const handleChangeSweets = () => {
		setSentense("");
		setMode("sweets");
		setDescription(
			"コンビニお菓子をキーワード検索できます。\nお題がお菓子だったのを思い出して、やっつけで付けました。"
		);
	};
	return (
		<div className="header">
			<div onClick={handleChangeLang}>自然言語計算</div>
			<div onClick={handleChangeSweets}>お菓子検索</div>
		</div>
	);
};

export default Header;
