import {
	Header,
	Title,
	Form,
	Results,
	SweetsSearch,
} from "../components/index";
import { useState } from "react";
const TopPage = ({
	loading,
	handleSubmit,
	handleSearch,
	sentense,
	setSentense,
	results,
	mode,
	setMode,
}) => {
	const [description, setDescription] = useState(
		"類似度の高い単語を表示します。<br>単語を足し引きすることもできます。"
	);
	return (
		<div className="top-page-container">
			<div>
				<Header
					setMode={setMode}
					setSentense={setSentense}
					setDescription={setDescription}
				/>
				<Title mode={mode} />
				{mode === "lang" ? (
					<Form
						sentense={sentense}
						setSentense={setSentense}
						handleSubmit={handleSubmit}
					/>
				) : (
					<SweetsSearch
						sentense={sentense}
						setSentense={setSentense}
						handleSearch={handleSearch}
					/>
				)}

				<Results
					loading={loading}
					results={results}
					mode={mode}
					description={description}
				/>
			</div>
		</div>
	);
};

export default TopPage;
